import { Component, OnInit } from '@angular/core';
import {IEmployee} from './IEmployee';
import {EmployeeService} from './employee.service';
import {Router} from '@angular/router'
import { CookieService } from 'ngx-cookie-service';
import { LocalStorageService, SessionStorageService, LocalStorage, SessionStorage } from 'angular-web-storage';
 

@Component({
  selector: 'app-list-employee',
  templateUrl: './list-employee.component.html',
  styleUrls: ['./list-employee.component.css']
})
export class ListEmployeeComponent implements OnInit {


  employees:IEmployee[];
  constructor(private _employeeService:EmployeeService,
    private _router:Router,private _cookiesService:CookieService,
    private _localstorage:LocalStorageService,private _sessionstorage:SessionStorageService) { }

  ngOnInit() {

    this._employeeService.getEmployees().subscribe(
      (listEmployees)=>this.employees=listEmployees,
      (err)=>console.log(err)
    )
    
  }

  setCookies(){
    this._cookiesService.set('test','testing cookies');
  }

  getCookies(){
    alert(this._cookiesService.get("test"));
  }

  deleteCookies(){
    this._cookiesService.delete("test");
  }

  setlocalstorage(){
    this._localstorage.set("userName","Kameshwar Gupta")
  }

  getlocalstorage(){
    alert(this._localstorage.get("userName"));
  }

  deletelocalstorage(){
    this._localstorage.clear();
  }
  
  setsessionstorage(){
    this._sessionstorage.set("logged-in","Kameshwar")
  }

  getsessionstorage(){
    alert(this._sessionstorage.get("logged-in"));
  }

  deletesessionstorage(){
    this._sessionstorage.clear();
  }

  editButtonClick(employeeId:number){
    console.log(this._employeeService.baseUrl);
    this._router.navigate(['/employees/edit',employeeId])
  }

  deleteButtonClick(employeeId:number){
    this._employeeService.deleteEmployee(employeeId).subscribe(      
      () => this._router.navigate(['employees']),
      (err: any) => console.log(err)
    );
    console.log(employeeId);
    
  }

  
}
